	var _this;
	import {feedbackInsert} from '@/static/js/API.js'
    export default {
        data() {
            return {
                msgContents: ["界面显示错乱", "启动缓慢，卡出翔了", "UI无法直视，丑哭了", "偶发性崩溃","课表不准确","绩点计算不准确"],
                stars: [1, 2, 3, 4, 5], 
                imageList: [],
                sendDate: {
                    score: 0,
                    content: "",
                    contact: "" 
                }
            }
        },
        onLoad() {
            let deviceInfo = {
                appid: plus.runtime.appid,
                imei: plus.device.imei, //设备标识
                p: plus.os.name === "Android" ? "a" : "i", //平台类型，i表示iOS平台，a表示Android平台。
                md: plus.device.model, //设备型号
                app_version: plus.runtime.version,
                plus_version: plus.runtime.innerVersion, //基座版本号
                os: plus.os.version,
                net: "" + plus.networkinfo.getCurrentType()
            }
            this.sendDate = Object.assign(deviceInfo, this.sendDate);
        },
        methods: {
            close(e){
                this.imageList.splice(e,1);
            },
            chooseMsg() { //快速输入
                uni.showActionSheet({
                    itemList: this.msgContents,
                    success: (res) => {
                        this.sendDate.content = this.msgContents[res.tapIndex];
                    }
                })
            },
            chooseImg() { //选择图片
                uni.chooseImage({
                    sourceType: ["camera", "album"],
                    sizeType: "compressed",
                    count: 5 - this.imageList.length,
                    success: (res) => {
                        this.imageList = this.imageList.concat(res.tempFilePaths);
                    }
                })
            },
            chooseStar(e) { //点击评星
                this.sendDate.score = e;
            },
            previewImage() { //预览图片
                uni.previewImage({
                    urls: this.imageList
                });
            },
            send() { //发送反馈
                console.log(JSON.stringify(this.sendDate));
				_this = this
                let imgs = this.imageList.map((value, index) => {
                    return {
                        name: "image" + index,
                        uri: value
                    }
                })
							 
			  for(let i = 0;i < imgs.length ; i++){
				  const uploadTask = uni.uploadFile({
				       url: feedbackInsert,
				       filePath:imgs[i].uri,
					   name:'filePath',
				  	   header:{
						   "Content-Type": "multipart/form-data",
						   "Referer":""
						   },
				       formData: {
							feedbackContent:_this.sendDate.content,
							rate:_this.sendDate.score,
							contact:_this.sendDate.contact
				  		},
				       success: (res) => {
				           if (res.statusCode === 200) {
				               uni.showToast({
				                   title: "反馈成功!",
								   success() {
								   	  _this.imageList = []
								   	  _this.sendDate = {
								   	      score: 0,
								   	      content: "",
								   	      contact: "" 
								   	  }
									  uni.hideLoading()
								   }
				               });
				           } 
				       },
				       fail: (res) => {
				           uni.showToast({
				               title: "失败",
				               icon:"none"
				           });
				           console.log(res)
				       }
				   })
				   
				   uploadTask.onProgressUpdate((res) => {
					   let progress = res.progress;
					   uni.showLoading({
					   	  title:`上传进度:${progress}`,
					   })
				   })
				   
				   if(i == imgs.length ){
					   this.imageList = [];
					   this.sendDate = {
					       score: 0,
					       content: "",
					       contact: ""
					   }
				   }
			   }	
            }
        }
    }
